.navbar-user {
  display: flex;
  justify-content: flex-end;
  align-items: center;

  &__btn {
    width: fit-content;

    &:nth-child(1) {
      margin-right: 8px;
      background-color: transparent;
      border: none;
      outline: none;
    }

    &:nth-child(2) {
      height: 32px;
      font-size: 14px;
      line-height: 14px;
      text-align: center;
      padding: 9px 16px;
      //color: #dcdcdc;
      background: #2d60e0;
      border-radius: 2px;
      border: none;
      display: inline-block;
    }
  }

  &__dropdown-group {
    display: flex;

    & > * {
      margin-right: 20px;
    }
  }
}
