.main {
  padding: 4em 0;
  background: url('../../assets/en-fg.png') no-repeat, #fafafa;
  background-position-x: 320px;

  &__header {
    text-align: center;
    font-size: 34px;
    color: var(--main-color);
    max-width: 800px;
    line-height: 1.8em;
    font-weight: bold;
    margin: 0 auto 1em;
  }

  &__input-box {
    display: flex;
    margin: 0 auto;
    width: 100%;
    max-width: 400px;

    & input,
    button {
      width: auto;
    }

    & input {
      flex-grow: 2;
      margin-right: 8px;
      -webkit-box-flex: 1;
      font-size: 16px;
      padding: 12px 26px;
      font-weight: 500;
      color: #1e2329;
      background-color: #f5f5f5;
      border: navajowhite;
      min-width: 50px;
      outline: none;
    }

    & button {
      flex-grow: 1;
      height: 46px;
      font-weight: 600;
      font-size: 14px;
      color: #1e2329;
      line-height: 20px;
      background: #fcd535;
      padding: 5px 26px;
      border-radius: 3px;
      border: none;
    }
  }

  &__link {
    color: inherit;
    text-decoration: none;
  }

  &__present-label {
    color: #1e2329;
    margin-top: 20px;
    line-height: 1.5;
    margin-bottom: 0;
    text-align: center;
  }
}
