@import 'styles/colors';

.auth {
  padding: 40px 0 30px;

  &__label {
    margin: 8px 0 5px;
  }

  &__btn-submit {
    border-radius: 2px;
    color: #fff;
    background-color: #2d60e0;
    border: 1px solid #2d60e0;
    -webkit-box-shadow: none;
    box-shadow: none;
    outline: 0;
    font-weight: 600;
    box-sizing: border-box;

    &:not(:disabled):hover {
      color: #fff;
      background-color: #5281f9;
      border: 1px solid #5281f9;
    }
  }

  &__btn {
    font-size: 12px;
    color: #3f475a;
    background-color: #fff;
    border: 1px solid #d8dce6;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0.1, 0.1);
    outline: none;

    &-mark {
      color: #2450be;
    }
  }

  &__text-safe {
    color: #02c076;
  }

  &__btn-group {
    margin-top: 8px;

    & button:nth-child(2) {
      margin-top: 15px;
    }
  }

  &__header {
    font-size: 30px;
    font-weight: 500;
    color: $White;
    line-height: 28px;
    text-align: center;
  }

  &__form-header {
    box-sizing: border-box;
    margin: 0 0 12px;
    min-width: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: rgb(118, 128, 143);
    text-align: center;
  }

  &__mark {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #3f475a;
    margin: auto;

    box-sizing: border-box;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    border: 1px solid rgb(245, 245, 245);
    border-radius: 23px;
    width: fit-content;
    padding: 8px 24px;
  }

  &__form {
    width: 420px;
    padding: 40px;
    box-sizing: border-box;
    margin: 40px auto 0;
    background-color: var(--main-background-color);
    box-shadow: 0 8px 24px 0 rgb(0, 0, 0.8);
    border-radius: 6px;
  }

  &__promise {
    font-size: 12px;
    color: rgba(0, 0, 0, 0.85);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    display: inline-block;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    line-height: 1.5;
    word-break: break-word;
    margin: 5px 0 15px 2px;
  }
}
