.dashboard {
  width: 100%;
  box-sizing: border-box;

  &__overview {
    display: flex;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
    padding: 25px 0 25px 30px;
    color: #1f2533;
    border: 1px solid #edf0f7;
    background-color: #fafafa;
  }

  &__user-info {
    color: #1f2533;
    padding-left: 18px;
  }

  &__text-main {
    font-weight: 600;
    font-size: 18px;
    line-height: 14px;
    margin-top: 5px;

    &_sub {
      margin-left: 10px;
      font-size: 14px;
      font-weight: 400;
    }
  }

  &__text-secondary {
    font-size: 13px;
    line-height: 1.5px;
    margin-top: 5px;
    color: #7b8293;

    & span:nth-child(1) {
      margin-right: 15px;
    }
  }
}
